<template>
  <form>
    <label>
      Name
      <input type="text" v-model="name" class="input">
    </label>
    <label>
      Email
      <input type="email" v-model="email" class="input">
    </label>
    <label>
      Date
      <input type="date" v-model="date" class="input">
    </label>
    <p v-if="validated" class="error">All fields must be filled</p>
    <button :disabled="validated" type="button" @click="save" class="btn">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill="none" d="M0 0h24v24H0V0z"/><path d="M9 16.2L4.8 12l-1.4 1.4L9 19 21 7l-1.4-1.4L9 16.2z"/></svg>
    </button>
    <button type="button" @click="cancel" class="btn">
      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path opacity=".87" fill="none" d="M0 0h24v24H0V0z"/><path d="M12 2C6.47 2 2 6.47 2 12s4.47 10 10 10 10-4.47 10-10S17.53 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm3.59-13L12 10.59 8.41 7 7 8.41 10.59 12 7 15.59 8.41 17 12 13.41 15.59 17 17 15.59 13.41 12 17 8.41z"/></svg>
    </button>
  </form>
</template>

<script>

export default {
  name: 'ItemForm',
  data: function() {
    return {
      name: '',
      email: '',
      date: '',
    }
  },
  computed: {
    validated: function() {
      return this.date === '' || this.name === '' || this.email ==='';
    }
  },
  methods: {
    save: function() {
      let id = this.$route.params.index
      if (id) {
        this.$store.dispatch('change', {
          vm: this,
          index: id,
          data: {
            name: this.name,
            email: this.email,
            date: this.date,
          }
        })
      } else {
        this.$store.dispatch('add', {
          vm: this,
          data: {
            name: this.name,
            email: this.email,
            date: this.date,
          }
        })
      }
      this.$router.push('/');
    },
    cancel: function() {
      this.$router.push('/');
    },
  },
  created: function() {
    let id = this.$route.params.index
    if (id) {
      const item = this.$store.getters.item(id)
      this.name = item.name
      this.email = item.email
      this.date = item.date
    } else {
      this.date = new Date().toISOString().slice(0, 10)
    }
  },
}
</script>

